import React, { Component } from 'react'
import { MDBContainer, MDBBtn, MDBModal, MDBModalBody, MDBModalHeader, MDBModalFooter } from 'mdbreact';
import { MDBInput } from "mdbreact";

export default class AddNewFamily extends Component {

  state={
    modal2: false,
  }
  
  toggle = nr => () => {
    let modalNumber = 'modal' + nr
    this.setState({
      [modalNumber]: !this.state[modalNumber]
    });
  }

  render() {
    return (
      <span>
      <span onClick={this.toggle(2)}><MDBBtn color="primary my-btn float-right" size="sm">Add New Family</MDBBtn></span>
      <MDBModal isOpen={this.state.modal2} toggle={this.toggle(2)}>
        <form action="#">
          <MDBModalHeader toggle={this.toggle(2)}>Add New Family</MDBModalHeader>
          <MDBModalBody>
            <MDBInput label="Family Code" />
            <MDBInput label="Family Name" />
            <MDBInput label="Family Address" />
          </MDBModalBody>
          <MDBModalFooter>
            <MDBBtn color="blue" onClick={this.toggle(2)}>Cancel</MDBBtn>
            <MDBBtn color="indigo darken-4" type="submit">Submit</MDBBtn>
          </MDBModalFooter>
        </form>
      </MDBModal>
    </span>
    )
  }
}
