import React, { Component } from 'react'
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from 'mdbreact';
import logo from './../../../assets/images/logo.png'
export class Login extends Component {
  
  constructor(props){
    super(props)
    this.state = {
      username: "",
      password: "",
    }
  }

  onclick = () =>{
    console.log("sample")
    window.location.href = "/"
  }

  render() {
    return (
      <div className="login-container">
        <MDBContainer>
          <MDBRow>
            <MDBCol md="6"  className="offset-md-3 login-form">
              <form><img src={logo} alt="logo"/>
                <p className="h3 mb-4 login-hospital-name"> RURAL HEALTH UNIT I</p>
                <p className="h5 mb-4 login-label">Login</p> 
                <div className="grey-text">
                  <MDBInput
                    label="Type your email"
                    group
                    type="email"
                    validate
                    error="wrong"
                    success="right"
                  />
                  <MDBInput
                    label="Type your password"
                    group
                    type="password"
                    validate
                  />
                </div>
                <div className="text-center">
                  <MDBBtn color="primary" onClick={() => this.onclick()}>Login</MDBBtn>
                </div>
              </form>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </div>
    )
  }
}

export default Login
